<template>
    <v-container class="todo-app">
      <v-row justify="center">
        <v-col cols="12" md="8">
          <h1 class="text-center">Todo List</h1>
          <v-form @submit.prevent="addTodo" class="todo-form">
            <v-text-field
              v-model="newTodo"
              placeholder="Add a new todo"
              outlined
              dense
            ></v-text-field>
            <v-btn type="submit" color="primary">Add</v-btn>
          </v-form>
          <v-list class="todo-list">
            <v-list-item
              v-for="todo in todos"
              :key="todo.ID"
              class="todo-item animate__animated animate__fadeIn"
            >
              <v-list-item-subtitle>
                <v-checkbox v-model="todo.completed" @change="updateTodo(todo)">
                  <span :class="{ completed: todo.completed }">
                    {{ todo.title }}
                  </span>
                </v-checkbox>
              </v-list-item-subtitle>
              <v-list-item-action>
                <v-btn icon @click="deleteTodoById(todo.ID)">
                  <v-icon color="error">mdi-delete</v-icon>
                </v-btn>
              </v-list-item-action>
            </v-list-item>
          </v-list>
        </v-col>
      </v-row>
    </v-container>
  </template>
  
  <script>
  export default {
    data() {
      return {
        todos: [],
        newTodo: ''
      }
    },
    methods: {
      async fetchTodos() {
        try {
          const response = await this.$http.get('/todos')
          this.todos = response.data
          console.log('Fetched todos:', this.todos)
        } catch (error) {
          console.error('Error fetching todos:', error)
        }
      },
      async addTodo() {
        try {
          const response = await this.$http.post('/todos', {
            title: this.newTodo,
            completed: false
          })
          this.todos.push(response.data)
          this.newTodo = ''
        } catch (error) {
          console.error('Error adding todo:', error)
        }
      },
      async updateTodo(todo) {
        try {
          await this.$http.put(`/todos/${todo.ID}`, todo)
        } catch (error) {
          console.error('Error updating todo:', error)
        }
      },
      async deleteTodoById(id) {
        if (!id) {
          console.error('The ID is undefined. Cannot delete the todo item.')
          return
        }
        try {
          const response = await this.$http.delete(`/todos/${id}`)
          console.log('Delete response:', response)
          this.todos = this.todos.filter(todo => todo.ID !== id)
          this.fetchTodos()
        } catch (error) {
          console.error('Error deleting todo:', error)
        }
      }
    },
    created() {
      this.fetchTodos()
    }
  }
  </script>
  
  <style scoped>
  .todo-app {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #f9f9f9;
  }
  
  .completed {
    text-decoration: line-through;
    color: #888;
  }
  </style>
  